<template>
  <div @scroll="loadMore($event)" class="BookCatalogView">
    <!-- -->
    <div v-if="bookInfo" class="top">
      <div class="left_img">
        <img
          :src="bookInfo.coverImage + thumbnail"
          alt=""
          @error="errorImg"
        />
      </div>
      <div class="right_txt">
        <div class="frame">
          <p>{{ bookInfo.title }}</p>
          <span>{{ bookInfo.author }}</span>
        </div>
      </div>
    </div>
    <!--  -->
    <div
      style="
        padding-left: 5%;
        text-align: left;
        margin-left: 10px;
        margin-bottom: 20px;
      "
    >
      {{ bookCatalog.length }}
    </div>
    <div class="catalog">
      <ul>
        <!--  -->
        <li
          v-for="(chapter, index) in bookCatalog.slice(startNum, endNum)"
          :key="chapter.secId"
          ref="chapter"
          @click="sendChapterIndex_secId(index, chapter.secId, chapter.needPay)"
        >
          <h2>
            <p>
              {{ chapter.title }}
              <span v-if="chapter.needPay" class="iconfont icon-suotou"></span>
            </p>
          </h2>
          <div class="dashline"></div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      // 
      bookCatalog: [],
      // 
      bookInfo: null,
      startNum: 0,
      endNum: 100,
      newList: [],
    };
  },
  props: ["highLighIndex"],
  watch: {
    // watch,,
    endNum: function (newvalue, oldvalue) {
      console.log(newvalue, oldvalue);
      // 
      this.$toast.loading({
        message: "...",
        forbidClick: true,
        loadingType: "circular",
        duration: 200,
      });
    },
    // ,,(,)
    highLighIndex: function (newVal, oldVal) {
      oldVal;
      this.highLighIndex = newVal;
      // 
      this.$nextTick(() => {
        this.updateHighLigth();
      });
    },
  },
  computed: {
    ...mapState(["source_uuid"]),
    ...mapState(["thumbnail"]),
  },

  updated() {
    this.$nextTick(() => {
      // 
      this.updateHighLigth();
    });
  },
  created() {
    // 
    this.$toast.loading({
      message: "...",
      forbidClick: true,
      loadingType: "circular",
      duration: 0,
    });
    // 
    this.axios
      .get(
        `https://apis.netstart.cn/yunyuedu/reader/book/info.json?source_uuid=${this.$route.query.id}`
      )
      .then((res) => {
        // bookCatalog
        this.bookCatalog = res.data.data.catalog;
        this.bookInfo = res.data.data.book;
        this.$toast.clear();
      });
  },
  // 
  activated() {
    if (this.source_uuid != this.$route.query.id) {
      console.log(
        "(),this.source_uuid != this.$route.query.id"
      );
      // 
      this.$toast.loading({
        message: "...",
        forbidClick: true,
        loadingType: "circular",
        duration: 0,
      });
      // 
      this.axios
        .get(
          `https://apis.netstart.cn/yunyuedu/reader/book/info.json?source_uuid=${this.$route.query.id}`
        )
        .then((res) => {
          // bookCatalog
          this.bookCatalog = res.data.data.catalog;
          this.bookInfo = res.data.data.book;
          this.$toast.clear();
        });
    }
  },
  // 
  deactivated() {},
  mounted() {
    // for (let i = 0; i < this.$refs.chapter.length; i++) {
    //   this.$refs.chapter[i].classList.remove("highLight");
    // }
    // // 
    // this.$refs.chapter[this.highLighIndex].classList.add("highLight");
  },
  methods: {
    // 
    errorImg(event) {
      let img = event.srcElement;
      img.src = require("@/assets/img/book_cover.png");
      img.onerror = null; //
    },
    // 
    updateHighLigth() {
      // 
      if (this.$refs.chapter) {
        for (let i = 0; i < this.$refs.chapter.length; i++) {
          this.$refs.chapter[i].classList.remove("highLight");
        }
        // 
        this.$refs.chapter[this.highLighIndex].classList.add("highLight");
      }
    },
    // 
    sendChapterIndex_secId(index, secId, needPay) {
      console.log(
        ":",
        index,
        "secId:",
        secId
      );
      // :IDindex,needPay
      this.$emit("clickChapter", index, secId, needPay);
      for (let i = 0; i < this.$refs.chapter.length; i++) {
        this.$refs.chapter[i].classList.remove("highLight");
      }
      // 
      this.$refs.chapter[index].classList.add("highLight");
    },

    // 
    loadMore(e) {
      // e.srcElement.scrollTop 
      // e.srcElement.offsetHeight 
      // e.srcElement.scrollHeight ,,,scrollHeight-offsetHeight
      /* console.log("e.srcElement.scrollTop", e.srcElement.scrollTop);
      console.log("e.srcElement.offsetHeight", e.srcElement.offsetHeight);
      console.log("e.srcElement.scrollHeight", e.srcElement.scrollHeight);*/

      if (
        e.srcElement.scrollTop + e.srcElement.offsetHeight >=
        e.srcElement.scrollHeight
      ) {
        if (this.endNum < this.bookCatalog.length) {
          this.endNum = this.endNum + 250;
        } else {
          this.$toast({
            message: "!",
            position: "bottom",
          });
        }
        // this.newList = this.bookCatalog.slice(this.startNum, this.endNum);
      }
    },
  },
};
</script>

<style lang="less" scoped>
.BookCatalogView {
  height: 100vh;
  overflow: scroll;
  .top {
    display: flex;
    margin-left: 5%;
    margin-top: 7%;
    .left_img {
      img {
      }
    }
    .right_txt {
      // background: red;
      // padding-top: 40px;
      margin-left: 5%;
      width: 100%;
      text-align: left;
      .frame {
        margin-top: 20px;
        // background: red;
        p {
          font-size: 20px;
          margin-bottom: 10px;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 1;
          overflow: hidden;
        }
        span {
          color: gray;
          font-size: 16px;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 1;
          overflow: hidden;
        }
      }
    }
  }
  .catalog {
    // height: 100%;
    // overflow: auto;
    ul {
      text-align: left;
      padding: 0 10px;
      li {
        padding-left: 5%;
        h2 {
          margin-bottom: 5px;
          p {
            position: relative;
            span {
              position: absolute;
              right: 20px;
            }
          }
        }

        .dashline {
          border: 1px gray dashed;
          margin-bottom: 5px;
        }
      }
    }
  }
  .highLight {
    color: red;
  }
}
</style>