<template>
  <!--  -->
  <div class="lkfg">
    <TitleBar :title="title" :totalCount="comment_num" />
    <Comment
      :commentResult="commentResult"
      :FatherSkeletonShow="FatherSkeletonShow"
    />
    <!-- <van-row style="background: red; text-align: center" class="loadMore">
      <van-col span="24">...</van-col>
    </van-row> -->
  </div>
</template>
<script>
import TitleBar from "../components/TitleBar.vue";
import Comment from "../components/Comment.vue";
import { mapState } from "vuex";
export default {
  components: { TitleBar, Comment },
  data() {
    return {
      title: "",
      // 
      // 
      pageNum: 1,
      commentResult: null,
      activatedId: null,
      FatherSkeletonShow: true,
    };
  },
  computed: {
    ...mapState(["comment_num"]),
    ...mapState(["activatedIdVuex"]),
    ...mapState(["source_uuid"]),
  },
  created() {
    console.log("created!");

    /*  this.$toast.loading({
      message: "...",
      forbidClick: true,
      loadingType: "spinner",
      position: "bottom",
      duration: 0,
    }); */
    this.getComment(this.$route.query.id).then((res) => {
      // console.log(res);
      // ?
      // ,,,,likes,,
      // :
      this.commentResult = res;
      // vuex
      this.$store.dispatch("getCommentResult", this.commentResult);

      /*  this.$toast.clear(); */
    });
  },
  methods: {
    getComment(id) {
      return this.axios.get(
        `https://apis.netstart.cn/yunyuedu/comment/getComments.json?bookId=${id}&page=${this.pageNum}`
      );
    },
  },

  mounted() {
    console.log("!");
  },
  // 
  activated() {
    // this.activatedId = this.activatedIdVuex;
    if (this.activatedId != this.$route.query.id && this.activatedId != null) {
      console.log(
        "this.activatedId != this.$route.query.id,"
      );

      this.$toast.loading({
        message: "...",
        forbidClick: true,
        loadingType: "spinner",
        position: "bottom",
        duration: 0,
      });
      this.getComment(this.$route.query.id).then((res) => {
        this.commentResult = res;
        // vuex
        this.$store.dispatch("getCommentResult", this.commentResult);

        this.$toast.clear();
      });
    }
  },
  // 
  deactivated() {
    console.log("BookcommentView!");
    // route.idactivatedId,
    this.activatedId = this.source_uuid;
    // VUEX
    this.$store.dispatch("getActivatedId", this.source_uuid);
  },
};
</script>

