<template>
  <div>
    <van-row style="padding: 0 5%">
      <van-col span="5">
        <p style="height: 45px; line-height: 45px; text-align: left"></p>
      </van-col>
      <van-col
        span="17"
        offset="1"
        style="height: 45px; line-height: 45px; text-align: right"
      >
        <span @click="EnterSearch">
          <i style="font-size: 22px" class="iconfont icon-sousuo"></i>
        </span>
        <!-- -------- -->
        <span style="margin-left: 10px">
          <van-popover
            v-model="showPopover"
            trigger="click"
            :actions="actions"
            @select="onSelect"
            placement="bottom-end"
            :overlay="true"
            :offset="[14, 8]"
          >
            <template #reference>
              <van-icon size="22" name="wap-nav" />
            </template>
          </van-popover>
        </span>
      </van-col>
    </van-row>
    <!--  -->
    <div ref="vancol" class="underline"></div>
    <!--  -->
    <van-row v-if="localBookShelfx == null || localBookShelfx == '[]'">
      <van-empty image="search" description="" />
      <van-col style="margin-top: 1vh" span="24">
        <van-button
          @click="goHome()"
          width="100"
          color="rgb(211 43 37);"
          round
          type="info"
          ></van-button
        >
      </van-col>
    </van-row>
    <!--  -->
    <van-row
      ref="vancol2"
      v-else
      gutter="20"
      style="
        padding: 0 5%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
      "
    >
      <van-col
        @touchstart.native="gtouchstart(item.id, $event)"
        @touchmove.native="gtouchmove($event)"
        @touchend.native="showDeleteButton(item.id, $event)"
        span="8"
        v-for="(item, index) in bookShelfList"
        :key="index"
        style="margin-bottom: 10px; width: 30%"
        class="book_item"
      >
        <img
          class="book_img"
          width="100%"
          :src="item.cover + '?imageView&thumbnail=240y336'"
          @error="errorImg"
        />

        <p
          class="book_name"
          style="
            text-align: left;
            font-size: 14px;
            margin-top: 10px;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            overflow: hidden; ;
          "
        >
          {{ item.title }}
        </p>
      </van-col>

      <van-col span="8" style="margin-bottom: 10px; width: 30%"> </van-col>
    </van-row>
    <!-- :----------- -->
    <van-popup
      position="bottom"
      v-model="longPressShow"
      style="text-align: left"
      @close="longPressShow = false"
    >
      <van-cell
        @click="goBookContent(popupBookId)"
        style="padding-left: 30px"
        title=""
        icon="orders-o"
      >
      </van-cell>
      <van-cell
        @click="goBookDeatil(popupBookId)"
        style="padding-left: 30px"
        title=""
        icon="description"
      >
      </van-cell>
      <van-cell
        @click="removeBook(popupBookId)"
        style="padding-left: 30px"
        title=""
        icon="delete-o"
      >
      </van-cell>
    </van-popup>
    <div class="bottom_trim" style="margin-top: 50px"></div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { Dialog } from "vant";
export default {
  components: { [Dialog.Component.name]: Dialog.Component },

  data() {
    return {
      // 
      longPressShow: false,
      // ID
      popupBookId: null,
      bookShelfList: [],
      activatedShelfLIst: [],
      // 
      showPopover: false,
      //  actions 
      actions: [
        { text: "", icon: "bars" },
        { text: "", icon: "clock-o" },
      ],
    };
  },
  computed: {
    ...mapState(["back_home_key"]),
    ...mapState(["localBookShelfx"]),
    // ...mapState(["source_uuid"]),
  },
  created() {},
  // 
  beforeRouteEnter(to, from, next) {
    to, from;
    next((vm) => {
      if (vm.activatedShelfLIst !== vm.localBookShelfx) {
        vm.$store.dispatch(
          "updateLocalBookShelf",
          localStorage.getItem("localShelf")
        );
        if (vm.localBookShelfx != null && vm.localBookShelfx != "[]") {
          vm.axios
            .get(
              `https://apis.netstart.cn/yunyuedu/shelf/detail.json?ids=${vm.localBookShelfx}`
            )
            .then((res) => {
              vm.bookShelfList = res.data.data.list.reverse();
            });
        }
      }
    });
  },
  beforeRouteLeave(to, from, next) {
    to, from;

    this.activatedShelfLIst = this.localBookShelfx;
    next();
  },
  // ////////
  methods: {
    // 
    errorImg(event) {
      let img = event.srcElement;
      img.src = require("@/assets/img/book_cover.png");
      img.onerror = null; //
    },
    // 
    removeBook(bookId) {
      bookId;
      // 
      this.longPressShow = false;
      Dialog.confirm({
        title: "",
        message: "?",
      })
        .then(() => {
          this.bookShelfList.forEach((item, index) => {
            if (item.id == bookId) {
              this.bookShelfList.splice(index, 1);
            }
          });

          var newLocalBookShelfx = JSON.parse(this.localBookShelfx);
          newLocalBookShelfx.forEach((item, index) => {
            if (item.id == bookId) {
              newLocalBookShelfx.splice(index, 1);
            }
          });

          this.$store.dispatch(
            "updateLocalBookShelf",
            JSON.stringify(newLocalBookShelfx)
          );
          localStorage.setItem("localShelf", this.localBookShelfx);
          this.$toast("");
        })

        .catch(() => {
          // on cancel
        });
    },
    // 
    goBookDeatil(bookId) {
      this.longPressShow = false;
      setTimeout(() => {
        this.$router.push({
          name: "bookdetail",
          query: {
            id: bookId,
          },
        });
      }, 100);
    },
    // 
    goBookContent(bookId) {
      this.longPressShow = false;
      setTimeout(() => {
        this.$router.push({
          path: "/bookcontent",
          query: { id: bookId },
        });
      }, 100);
    },
    //
    gtouchstart(bookId, e) {
      e;
      console.log("!");

      var self = this;
      this.timeOutEvent = setTimeout(function () {
        self.longPress(bookId);
      }, 500); //500
      return false;
    },
    //500onclick
    showDeleteButton(bookId, e) {
      clearTimeout(this.timeOutEvent); //
      if (this.timeOutEvent != 0) {
        //onclick
        console.log("");
        this.goBookContent(bookId);
      }
      if (e.cancelable) {
        e.preventDefault(); //
      }
    },
    //
    gtouchmove(e) {
      e;
      clearTimeout(this.timeOutEvent); //
      this.timeOutEvent = 0;
    },
    //
    longPress(bookId) {
      bookId;
      // ID,
      this.popupBookId = bookId;
      this.timeOutEvent = 0;
      //
      this.longPressShow = true;
    },
    // 
    onSelect(action, index) {
      if (index == 0) {
        console.log(action.text, index);
        setTimeout(() => {
          this.$router.push({
            name: "batchmanage",
          });
        }, 100);
      } else if (index == 1) {
        console.log(action.text, index);
        setTimeout(() => {
          this.$router.push({
            name: "readhistory",
          });
        }, 100);
      }
    },
    // 
    EnterSearch() {
      this.$router.push({
        name: "searchHint",
      });
    },
    // 
    goHome() {
      this.$router.push(this.back_home_key).catch(() => {});
    },
  },
  mounted() {},
};
</script>

<style lang="less">
.van-button--info {
  width: 100px;
}
@popover-action-width {
  width: 218px;
}
// div,,
.underline::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background: #fffafa;
  transform: scaleY(1);
  margin-bottom: 6px;
}
</style>
