<template>
  <!--  -->
  <div style="background: #ffffff">
    <!--  -->
    <van-dropdown-menu v-if="!isShowTip">
      <van-dropdown-item
        v-model="value1"
        :options="option1"
        @change="getNewSearchList"
      />
      <van-dropdown-item
        v-model="value2"
        :options="option2"
        @change="getNewSearchList"
      />
      <van-dropdown-item title="" ref="item">
        <van-row class="choose">
          <van-col span="22" offset="1">
            <div class="book_state">
              <div class="state">
                <p></p>
              </div>
              <div class="state_select">
                <div
                  @click="stateSelect(index)"
                  v-for="(stateItem, index) in book_state"
                  :key="index"
                  :class="{ activeLight: activeState == index }"
                >
                  {{ stateItem.text }}
                </div>
              </div>
            </div>
            <div class="book_price">
              <div class="price">
                <p></p>
              </div>
              <div class="price_select">
                <div
                  @click="priceSelect(index)"
                  v-for="(priceItem, index) in book_price"
                  :key="index"
                  :class="{ activeLight: activePrice == index }"
                >
                  {{ priceItem.text }}
                </div>
              </div>
            </div>
            <div class="book_words">
              <div class="words">
                <p></p>
              </div>
              <div class="words_select">
                <div
                  @click="wordsSelect(index)"
                  v-for="(wordItem, index) in book_words"
                  :key="index"
                  :class="{ activeLight: activeWords == index }"
                >
                  {{ wordItem.text }}
                </div>
              </div>
            </div>
          </van-col>
        </van-row>
      </van-dropdown-item>
    </van-dropdown-menu>

    <van-row style="background: #f7f8fa">
      <van-col span="22" offset="1">
        <p style="text-align: left; padding: 10px 0"></p>
      </van-col>
    </van-row>
    <van-list
      v-model="loading"
      :finished="finished"
      :error.sync="error"
      error-text=""
      @load="onLoad"
      style="background: #ffffff; height: calc(100vh - 143px"
    >
      <div v-if="searchList.length > 0" class="column_three">
        <ul>
          <!--  -->
          <li
            v-for="(item, index) in searchList"
            :key="index"
            @click="enterBookDetail(item.bookId)"
          >
            <!--  -->
            <img
              :src="item.cover + thumbnail"
              alt=""
              @error="errorImg"
            />
            <!--  -->
            <h3>{{ item.title }}</h3>
            <!-- / -->
            <p>{{ item.author }}/{{ item.category }}</p>
            <!--  -->
            <p>
              {{ item.summary }}
            </p>
          </li>
        </ul>
        <div class="underline"></div>
      </div>

      <van-empty
        v-if="isShowTip"
        image="search"
        description=""
      />

      <div v-if="isShowTip" style="height: 48px"></div>
    </van-list>
    <div
      @click="backTop"
      v-show="flag_scroll"
      style="
        width: 30px;
        height: 30px;
        position: fixed;
        right: 13px;
        bottom: 30px;
        background: rgb(214 225 199 / 40%);
        border-radius: 50%;
        line-height: 30px;
        z-index: 3;
      "
    >
      <span style="font-size: 20px; color: #308ae3"
        ><i class="iconfont icon-dingbu"></i
      ></span>
    </div>
  </div>
</template>
<style lang="less" >
.choose {
  background: #f7f8fa;
  // 
  .book_state {
    .state {
      text-align: left;
      margin: 10px 0 0;
    }
    .state_select {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 10px;
      div {
        background: #ffffff;
        color: gray;
        width: 4rem;
        border-radius: 0.2rem;
        padding: 5px 10px;
        margin-right: 20px;
        margin-top: 10px;
      }
    }
  }
  // 
  .book_price {
    .price {
      text-align: left;
      margin: 20px 0 0;
    }
    .price_select {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 10px;
      div {
        background: #ffffff;
        color: gray;
        width: 4rem;
        border-radius: 0.2rem;
        padding: 5px 10px;
        margin-right: 20px;
        margin-top: 10px;
      }
    }
  }
  // 
  .book_words {
    .words {
      text-align: left;
      margin: 20px 0 0;
    }
    .words_select {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 10px;
      div {
        background: #ffffff;
        color: gray;
        margin-top: 10px;
        width: 8rem;
        border-radius: 0.2rem;
        padding: 5px 10px;
        margin-right: 20px;
      }
      div:nth-child(1) {
        background: #ffffff;
        color: gray;
        width: 4rem;
        border-radius: 0.2rem;
        padding: 5px 10px;
        margin-right: 20px;
        margin-top: 10px;
      }
    }
  }
}
.activeLight {
  background: #ff5a5f !important;
  color: #ffffff !important;
}
// 
.van-dropdown-menu__bar {
  box-shadow: 0 0px 0px #ccc !important;
}
.column_three {
  margin-left: 2vw;
  margin-right: 2vw;
  li {
    position: relative;
    z-index: 2;
    margin-bottom: 25px;
    display: list-item;
    text-align: -webkit-match-parent;
    img {
      text-align: left;
      float: left;
      width: 82px;
      height: 115px;
      margin-right: 15px;
      box-shadow: 0 5px 7px 0 rgb(0 0 0 / 9%), 0px 2px 2px 0 rgb(0 0 0 / 3%);
      border-radius: 4px;
    }
    h3 {
      text-align: left;
      font-weight: bold;
      font-size: 17px;
      color: #222;
      height: 20px;
      line-height: 20px;
      overflow: hidden;
      margin-bottom: 7px;
      text-overflow: ellipsis;
      white-space: nowrap;
      display: block;
      margin-block-start: 1em;
      margin-block-end: 1em;
      margin-inline-start: 0px;
      margin-inline-end: 0px;
    }
    p:nth-of-type(1) {
      text-align: left;
      line-height: 14px;
      height: 14px;
      margin-bottom: 9px;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      font-size: 12px;
      color: #999;
      display: block;
      margin-block-start: 1em;
      margin-block-end: 1em;
      margin-inline-start: 0px;
      margin-inline-end: 0px;
    }
    p:nth-of-type(2) {
      text-align: left;
      line-height: 18px;
      height: 36px;
      -webkit-line-clamp: 2;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
      font-size: 12px;
      color: #999;
      margin-block-start: 1em;
      margin-block-end: 1em;
      margin-inline-start: 0px;
      margin-inline-end: 0px;
    }
  }
}
</style>
<style >
</style>
<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      searchList: [],
      loading: false,
      finished: false,
      // 
      isShowTip: false,
      // 
      error: false,
      // 
      flag_scroll: false,
      // 
      value1: 0,
      value2: 0,
      option1: [
        { text: "", value: 0 },
        { text: "", value: 1 },
        { text: "", value: 2 },
        { text: "", value: 3 },
        { text: "", value: 4 },
      ],
      option2: [
        { text: "", value: 0 },
        { text: "", value: 1 },
        { text: "", value: 2 },
        { text: "", value: 3 },
      ],
      book_state: [
        { text: "", value: 0 },
        { text: "", value: 1 },
        { text: "", value: 2 },
      ],
      book_price: [
        { text: "", value: 0 },
        { text: "", value: 1 },
        { text: "", value: 2 },
        { text: "", value: 3 },
      ],
      book_words: [
        { text: "", value: 0 },
        { text: "30", value: 1 },
        { text: "30-50", value: 2 },
        { text: "50-100", value: 3 },
        { text: "100", value: 4 },
      ],
      // ,,
      activeState: 0,
      activePrice: 0,
      activeWords: 0,
      page: 1,
    };
  },
  props: ["keyword"],
  watch: {
    keyword(newVal, oldVal) {
      oldVal;
      this.keyword = newVal;
    },
  },
  computed: {
    ...mapState(["thumbnail"]),
    // ...mapState(["column_three_book_sort"]),
  },
  methods: {
    // ,
    enterBookDetail(bookId) {
      this.$router.push({
        name: "bookdetail",
        query: {
          id: bookId,
        },
      });
    },
    // 
    backTop() {
      window.scrollTo(0, 0);
    },
    // 
    errorImg(event) {
      let img = event.srcElement;
      img.src = require("@/assets/img/book_cover.png");
      img.onerror = null; //
    },
    // value,
    getNewSearchList() {
      // 
      this.searchList = [];
      this.page = 1;
      this.axios
        .get(
          `https://apis.netstart.cn/yunyuedu/search/book.json?keyword=${this.keyword}&sort=${this.value1}&channel=${this.value2}&bookStatus=${this.activeState}&pay=${this.activePrice}&wordCount=${this.activeWords}&page=${this.page}`
        )
        .then((res) => {
          this.$nextTick(() => {
            // 
            console.log("getNewSearchList");
            // ,,finishedtrue,
            this.finished = false;
            this.searchList.push(...res.data.data.books);
            this.loading = false;
            // 
            if (this.searchList.length == 0) {
              this.isShowTip = true;
            }

            if (res.data.data.page == undefined) {
              this.finished = true;
            } else {
              this.page = res.data.data.page;
            }
            //
          });
        });
    },

    // 
    stateSelect(index) {
      this.activeState = index;
      this.getNewSearchList();
    },
    // 
    priceSelect(index) {
      this.activePrice = index;
      this.getNewSearchList();
    },
    // 
    wordsSelect(index) {
      this.activeWords = index;
      this.getNewSearchList();
    },
    onLoad() {
      // 
      this.axios
        .get(
          `https://apis.netstart.cn/yunyuedu/search/book.json?keyword=${this.keyword}&sort=${this.value1}&channel=${this.value2}&bookStatus=${this.activeState}&pay=${this.activePrice}&wordCount=${this.activeWords}&page=${this.page}`
        )
        .then((res) => {
          this.$nextTick(() => {
            // 
            console.log("load");
            this.searchList.push(...res.data.data.books);
            this.loading = false;
            // 
            if (this.searchList.length == 0) {
              this.isShowTip = true;
            }

            // 
            if (res.data.data.page == undefined) {
              this.$toast({
                message: "()",
                position: "",
                duration: 1000,
              });
              this.finished = true;
            } else {
              this.page = res.data.data.page;
            }
            //
          });
        })
        .catch((err) => {
          err;
          this.error = true;
          this.loading = false;
        });
    },
    // 
    handleScroll() {
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (scrollTop > 700) {
        this.flag_scroll = true;
      } else {
        this.flag_scroll = false;
      }
    },
  },
  // 
  mounted() {
    window.addEventListener("scroll", this.handleScroll, true);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll); //
  },
};
</script>

