<template>
  <!--  -->
  <van-pull-refresh
    success-text="!"
    v-model="isLoading"
    :head-height="80"
    @refresh="onRefresh"
  >
    <div>
      <!-- -->
      <van-swipe
        v-if="banner_cover"
        class="my-swipe"
        :autoplay="3000"
        indicator-color="#1baeae"
      >
        <van-swipe-item
          @click="enterBannerBookContent(item)"
          v-for="(item, index) in banner_cover"
          :key="index"
        >
          <img class="swipe-img" :src="item.cover" alt="" />
        </van-swipe-item>
      </van-swipe>

      <!--  -->
      <!-- <div class="underline"></div> -->

      <!--  3 : -->
      <ColumnThree :column_three_book_sort="column_three_book_sort" />

      <!--  3 : -->
      <ColumnThree :column_three_book_sort="column_three_book_sort2" />

      <!-- 3  :24-->
      <RowThree :row_three_book_sort="row_three_book_sort" />

      <!--  3 :-->
      <ColumnThree :column_three_book_sort="column_three_book_sort3" />

      <!--  : -->
      <RowFour :row_four_book_sort="row_four_book_sort" />

      <!--  :IP -->
      <RowFour :row_four_book_sort="row_four_book_sort2" />

      <!--  3 : -->
      <RowThree :row_three_book_sort="row_three_book_sort2" />

      <!--   : -->

      <!--   : -->
    </div>
    <BaseLine v-if="banner_cover.length > 0" />
    <div class="bottom_trim"></div>
  </van-pull-refresh>
</template>

<script>
import { mapState } from "vuex";

import ColumnThree from "../components/ColumnThree.vue";
import RowThree from "../components/RowThree.vue";
import BaseLine from "./BaseLine.vue";
import RowFour from "./RowFour.vue";
export default {
  components: {
    ColumnThree,
    RowThree,
    BaseLine,
    RowFour,
  },
  data() {
    return {
      ctype: 1,
      // highly_recommended_week: {},
      information: [],
      banner_cover: [],
      shortCut: [],
      isLoading: false,
      // 
      column_three_book_sort: null,
      // 
      column_three_book_sort2: null,
      // 
      row_three_book_sort: null,
      // 
      column_three_book_sort3: null,

      //14
      row_four_book_sort: null,

      //24
      row_four_book_sort2: null,

      // 
      row_three_book_sort2: null,
    };
  },

  methods: {
    // 
    enterBannerBookContent(book) {
      // 
      if (book.id) {
        this.$router.push({
          path: "/bookcontent",
          query: { id: book.id, homeKey: "/bookmall/female" },
        });
      } else {
        // 
        this.$router.push({
          name: "jubensha",
          query: { url: book.url, homeKey: "/bookmall/female" },
        });
      }
    },
    // 
    onRefresh() {
      setTimeout(() => {
        this.axios
          .get(
            `https://apis.netstart.cn/yunyuedu/store/navi.json?gender=${this.genderNum}`
          )
          .then((res) => {
            // console.log(res);
            this.information = res.data.data.navi;

            // 
            this.axios
              .get(
                `https://apis.netstart.cn/yunyuedu/${this.information[3].url}`
              )
              .then((result) => {
                // 
                this.banner_cover = result.data.data.list[0].banners;
                // 
                this.column_three_book_sort = result.data.data.list[1];

                // 
                this.column_three_book_sort2 = result.data.data.list[2];

                // 
                this.row_three_book_sort = result.data.data.list[3];

                // 
                this.column_three_book_sort3 = result.data.data.list[4];

                // 14
                this.row_four_book_sort = result.data.data.list[5];

                // 24
                this.row_four_book_sort2 = result.data.data.list[6];

                //
                this.row_three_book_sort2 = result.data.data.list[7];
              });
          });
        this.isLoading = false;
      }, 1000);
    },
  },
  beforeRouteEnter(to, from, next) {
    // ..
    to, from;
    next((vm) => {
      vm.$store.dispatch("getBookmallNavIndex", 3);
    });
  },
  beforeRouteLeave(to, from, next) {
    // ,
    this.$store.dispatch("getBackHomeKey", from.path);

    next();
  },
  created() {
    //
    this.$toast.loading({
      message: "...",
      forbidClick: true,
      loadingType: "spinner",
      duration: 0,
      five_book_sort: null,
    });
    this.axios
      .get(
        `https://apis.netstart.cn/yunyuedu/store/navi.json?gender=${this.genderNum}`
      )
      .then((res) => {
        // console.log(res);

        this.information = res.data.data.navi;
        console.log("selected-nav-information===>", this.information);
        // 
        this.axios
          .get(`https://apis.netstart.cn/yunyuedu/${this.information[3].url}`)
          .then((result) => {
            // 
            this.banner_cover = result.data.data.list[0].banners;
            // 
            this.column_three_book_sort = result.data.data.list[1];

            // 
            this.column_three_book_sort2 = result.data.data.list[2];

            // 
            this.row_three_book_sort = result.data.data.list[3];

            // 
            this.column_three_book_sort3 = result.data.data.list[4];

            // 14
            this.row_four_book_sort = result.data.data.list[5];

            // 24
            this.row_four_book_sort2 = result.data.data.list[6];

            //
            this.row_three_book_sort2 = result.data.data.list[7];

            this.$toast.clear();
          });
      });
  },
  computed: {
    ...mapState(["genderNum"]),
  },
  watch: {
    // watch,,
    genderNum: function (newvalue, oldvalue) {
      console.log(newvalue, oldvalue);

      // 
      this.$toast.loading({
        message: "...",
        forbidClick: true,
        loadingType: "circular",
      });
      this.axios
        .get(
          `https://apis.netstart.cn/yunyuedu/store/navi.json?gender=${newvalue}`
        )
        .then((res) => {
          // console.log(res);
          this.information = res.data.data.navi;
          console.log("selected-nav-information===>", this.information);
          // 
          this.axios
            .get(`https://apis.netstart.cn/yunyuedu/${this.information[3].url}`)
            .then((result) => {
              // 
              this.banner_cover = result.data.data.list[0].banners;
              // 
              this.column_three_book_sort = result.data.data.list[1];

              // 
              this.column_three_book_sort2 = result.data.data.list[2];

              // 
              this.row_three_book_sort = result.data.data.list[3];

              // 
              this.column_three_book_sort3 = result.data.data.list[4];

              // 14
              this.row_four_book_sort = result.data.data.list[5];

              // 24
              this.row_four_book_sort2 = result.data.data.list[6];

              //
              this.row_three_book_sort2 = result.data.data.list[7];
            });
        });
    },
  },
};
</script>
<style lang="less" scoped>
// div
.bottom_trim {
  content: "";
  height: 50px;
  display: block;
}
.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  // width: 100%;
  // height: 150px;
  text-align: center;
  /* background-color: #39a9ed; */
}

.swipe-img {
  // width: 100%;
  // height: 150px;
}
.ranking_nav {
  ul {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 10px;
    margin-bottom: 10px;
    li {
      .yuan_color {
        width: 50px;
        height: 50px;
      }
    }
  }
}
// div,,
.underline::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background: #fffafa;
  transform: scaleY(1);
}
</style>
