<template>
  <van-pull-refresh
    success-text="!"
    v-model="isLoading"
    :head-height="80"
    @refresh="onRefresh"
  >
    <div>
      <!-- -->
      <van-swipe
        v-if="banner_cover"
        class="my-swipe"
        :autoplay="3000"
        indicator-color="#1baeae"
      >
        <!-- -->
        <van-swipe-item
          @click="enterBannerBookContent(item)"
          v-for="(item, index) in banner_cover"
          :key="index"
        >
          <img class="swipe-img" :src="item.cover" alt="" />
        </van-swipe-item>
      </van-swipe>

      <!--  -->
      <div class="ranking_nav">
        <ul>
          <li
            v-for="(item, index) in shortCut"
            :key="index"
            @click="enterChannelRank(item.url, item.title)"
          >
            <div class="yuan_color">
              <div class="rangking_icon">
                <img :src="item.cover" alt="" />
              </div>
            </div>
            <span>{{ item.name }}</span>
          </li>
        </ul>
      </div>
      <!--  -->
      <div class="underline"></div>
      <!-- 51 : -->
      <FiveBook :five_book_sort="five_book_sort" />
      <!-- 31 : -->
      <RowThree :row_three_book_sort="row_three_book_sort" />
      <!-- 52 : -->
      <FiveBook :five_book_sort="five_book_sort2" />
      <!-- 1 : -->
      <RowFour :row_four_book_sort="row_four_book_sort" />

      <!-- 31:  -->
      <ColumnThree :column_three_book_sort="column_three_book_sort" />
      <!--  -->

      <!-- 2 : -->
      <RowFour :row_four_book_sort="row_four_book_sort2" />
      <!-- 53 : -->
      <FiveBook :five_book_sort="five_book_sort3" />
      <!-- 3 : -->
      <RowFour :row_four_book_sort="row_four_book_sort3" />
      <!-- 32 :-->
      <ColumnThree :column_three_book_sort="column_three_book_sort2" />
      <!-- 33 : -->
      <ColumnThree :column_three_book_sort="column_three_book_sort3" />
      <!-- 34 :-->
      <ColumnThree :column_three_book_sort="column_three_book_sort4" />

      <!--  -->
    </div>
    <BaseLine v-if="banner_cover.length > 0" />

    <div class="bottom_trim"></div>
  </van-pull-refresh>
</template>

<script>
import { mapState } from "vuex";
import FiveBook from "../components/FiveBook.vue";
import ColumnThree from "../components/ColumnThree.vue";
import RowThree from "../components/RowThree.vue";
import BaseLine from "./BaseLine.vue";
import RowFour from "./RowFour.vue";
export default {
  components: {
    FiveBook,
    ColumnThree,
    RowThree,
    BaseLine,
    RowFour,
  },
  data() {
    return {
      ctype: 1,
      // highly_recommended_week: {},
      information: [],
      banner_cover: [],
      shortCut: [],
      isLoading: false,
      //  51 : 
      five_book_sort: null,
      //  31 :
      row_three_book_sort: null,
      //  52 : 
      five_book_sort2: null,
      //  1 :
      row_four_book_sort: null,
      //  31: 
      column_three_book_sort: null,
      //  

      //  2 :
      row_four_book_sort2: null,
      //  53 : 
      five_book_sort3: null,
      //  3 :
      row_four_book_sort3: null,
      //  32 :
      column_three_book_sort2: null,
      //  33 :
      column_three_book_sort3: null,
      //  34 :
      column_three_book_sort4: null,

      //  
    };
  },
  beforeRouteEnter(to, from, next) {
    // ..
    to, from;
    next((vm) => {
      vm.$store.dispatch("getBookmallNavIndex", 4);
    });
  },
  beforeRouteLeave(to, from, next) {
    // ,
    this.$store.dispatch("getBackHomeKey", from.path);

    next();
  },
  methods: {
    // 
    enterBannerBookContent(book) {
      // 
      if (book.id) {
        this.$router.push({
          path: "/bookcontent",
          query: { id: book.id, homeKey: "/bookmall/publish" },
        });
      } else {
        // 
        this.$router.push({
          name: "jubensha",
          query: { url: book.url, homeKey: "/bookmall/publish" },
        });
      }
    },
    // 
    enterChannelRank(url, title) {
      if (title == "") {
        title = "";
      } else if (title == "") {
        title = "";
      } else if (title == "") {
        title = "";
      }

      if (
        url.indexOf("bookList.json?uuid=fde4e44") !== -1 ||
        url.indexOf("bookList.json?uuid=62eea99") !== -1 ||
        url.indexOf("bookList.json?uuid=ecd8b35") !== -1
      ) {
        this.$router.push({
          name: "publishrank",
          query: {
            url: url,
            title: title,
            homeKey: "/bookmall/publish",
          },
        });
      } else {
        this.$router.push({
          name: "channelrank",
          query: {
            url: url,
          },
        });
      }
    },
    // 
    onRefresh() {
      setTimeout(() => {
        this.axios
          .get(
            `https://apis.netstart.cn/yunyuedu/store/navi.json?gender=${this.genderNum}`
          )
          .then((res) => {
            // console.log(res);
            this.information = res.data.data.navi;
            console.log("selected-nav-information===>", this.information);
            // 
            this.axios
              .get(
                `https://apis.netstart.cn/yunyuedu/${this.information[4].url}`
              )
              .then((result) => {
                // 
                this.banner_cover = result.data.data.list[0].banners;

                // 
                this.shortCut = result.data.data.list[1].shortCut;
                //  51 : 
                this.five_book_sort = result.data.data.list[2];
                //  31 :
                this.row_three_book_sort = result.data.data.list[3];
                //  52 : 
                this.five_book_sort2 = result.data.data.list[4];
                //  1 :
                this.row_four_book_sort = result.data.data.list[5];
                //  31: 
                this.column_three_book_sort = result.data.data.list[6];
                //  

                //  2 :
                this.row_four_book_sort2 = result.data.data.list[8];
                //  53 : 
                this.five_book_sort3 = result.data.data.list[9];
                //  3 :
                this.row_four_book_sort3 = result.data.data.list[10];
                //  32 :
                this.column_three_book_sort2 = result.data.data.list[11];
                //  33 :
                this.column_three_book_sort3 = result.data.data.list[12];
                //  34 :
                this.column_three_book_sort4 = result.data.data.list[13];

                //  
              });
          });
        this.isLoading = false;
      }, 1000);
    },
  },

  created() {
    //
    this.$toast.loading({
      message: "...",
      forbidClick: true,
      loadingType: "spinner",
      duration: 0,
      five_book_sort: null,
    });
    this.axios
      .get(
        `https://apis.netstart.cn/yunyuedu/store/navi.json?gender=${this.genderNum}`
      )
      .then((res) => {
        // console.log(res);

        this.information = res.data.data.navi;
        console.log("selected-nav-information===>", this.information);
        // 
        this.axios
          .get(`https://apis.netstart.cn/yunyuedu/${this.information[4].url}`)
          .then((result) => {
            // 
            this.banner_cover = result.data.data.list[0].banners;

            // 
            this.shortCut = result.data.data.list[1].shortCut;
            //  51 : 
            this.five_book_sort = result.data.data.list[2];
            //  31 :
            this.row_three_book_sort = result.data.data.list[3];
            //  52 : 
            this.five_book_sort2 = result.data.data.list[4];
            //  1 :
            this.row_four_book_sort = result.data.data.list[5];
            //  31: 
            this.column_three_book_sort = result.data.data.list[6];
            //  

            //  2 :
            this.row_four_book_sort2 = result.data.data.list[8];
            //  53 : 
            this.five_book_sort3 = result.data.data.list[9];
            //  3 :
            this.row_four_book_sort3 = result.data.data.list[10];
            //  32 :
            this.column_three_book_sort2 = result.data.data.list[11];
            //  33 :
            this.column_three_book_sort3 = result.data.data.list[12];
            //  34 :
            this.column_three_book_sort4 = result.data.data.list[13];

            //  
            this.$toast.clear();
          });
      });
  },
  mounted() {
    window.addEventListener("popstate", this.goBack, false);
  },
  destroyed() {
    window.removeEventListener("popstate", this.goBack, false);
  },
  computed: {
    ...mapState(["genderNum"]),
  },
  watch: {
    // watch,,
    genderNum: function (newvalue, oldvalue) {
      console.log(newvalue, oldvalue);

      // 
      this.$toast.loading({
        message: "...",
        forbidClick: true,
        loadingType: "circular",
      });
      this.axios
        .get(
          `https://apis.netstart.cn/yunyuedu/store/navi.json?gender=${newvalue}`
        )
        .then((res) => {
          // console.log(res);
          this.information = res.data.data.navi;
          console.log("selected-nav-information===>", this.information);
          // 
          this.axios
            .get(`https://apis.netstart.cn/yunyuedu/${this.information[4].url}`)
            .then((result) => {
              // 
              this.banner_cover = result.data.data.list[0].banners;

              // 
              this.shortCut = result.data.data.list[1].shortCut;
              //  51 : 
              this.five_book_sort = result.data.data.list[2];
              //  31 :
              this.row_three_book_sort = result.data.data.list[3];
              //  52 : 
              this.five_book_sort2 = result.data.data.list[4];
              //  1 :
              this.row_four_book_sort = result.data.data.list[5];
              //  31: 
              this.column_three_book_sort = result.data.data.list[6];
              //  

              //  2 :
              this.row_four_book_sort2 = result.data.data.list[8];
              //  53 : 
              this.five_book_sort3 = result.data.data.list[9];
              //  3 :
              this.row_four_book_sort3 = result.data.data.list[10];
              //  32 :
              this.column_three_book_sort2 = result.data.data.list[11];
              //  33 :
              this.column_three_book_sort3 = result.data.data.list[12];
              //  34 :
              this.column_three_book_sort4 = result.data.data.list[13];

              //  
            });
        });
    },
  },
};
</script>
<style lang="less" scoped>
// div
.bottom_trim {
  content: "";
  height: 50px;
  display: block;
}
.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  // width: 100%;
  // height: 150px;
  text-align: center;
  /* background-color: #39a9ed; */
}

.swipe-img {
  // width: 100%;
  // height: 150px;
}
.ranking_nav {
  ul {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 10px;
    margin-bottom: 10px;
    li {
      .yuan_color {
        width: 50px;
        height: 50px;
      }
    }
  }
}
// div,,
.underline::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background: #fffafa;
  transform: scaleY(1);
}
</style>
