<template>
  <div>
    <!--  -->
    <div style="height: 86px"></div>
    <transition
      name="animate__animated animate__bounce"
      leave-active-class="animate__fadeOutUp"
    >
      <div
        v-show="zhanweiShow"
        :style="{ height: shaixuanHeight, background: 'red' }"
      ></div>
    </transition>

    <!--  -->

    <div
      v-if="sortList.length > 0"
      style="
        text-align: left;
        position: fixed;
        top: 0;
        width: 100vw;
        background: white;
      "
    >
      <TitleBar :title="$route.query.title" />
      <!--  -->
      <div
        style="
          display: flex;
          margin: 0 5%;
          position: relative;
          flex-wrap: wrap;
          height: 40px;
          line-height: 40px;
        "
      >
        <p
          @click="select(sortList[0].value, 'sort')"
          :class="{ activeCss: sortValue == sortList[0].value }"
          style="margin-right: 10px"
        >
          {{ sortList[0].name }}
        </p>
        <span style="margin-right: 10px">|</span>
        <p
          @click="select(sortList[1].value, 'sort')"
          :class="{ activeCss: sortValue == sortList[1].value }"
        >
          {{ sortList[1].name }}
        </p>
        <p
          style="text-align: right; position: absolute; right: 0"
          @click="toshow"
        >
          <span
            v-if="!shaixuan"
            style="
              border: 1px solid black;
              padding: 2px 10px 2px 10px;
              border-radius: 10px;
            "
          >
            </span
          >
          <span
            v-else
            style="
              border: 1px solid black;
              padding: 2px 10px 2px 10px;
              border-radius: 10px;
            "
          >
            </span
          >
        </p>
      </div>
      <transition
        name="animate__animated animate__bounce"
        enter-active-class="animate__fadeIn"
        leave-active-class="animate__fadeOutUp"
      >
        <!-- <span style="margin-right: 10px"></span> -->
        <!--  -->
        <div v-show="shaixuan" style="margin: 0 5% 5px 5%" ref="tanchuang">
          <!--  -->
          <div style="display: flex; flex-wrap: wrap">
            <p
              v-for="pay in payList"
              style="margin-right: 10px; margin-bottom: 5px"
              :key="pay.value"
              :class="{ activeCss: payValue == pay.value }"
              @click="select(pay.value, 'pay')"
            >
              {{ pay.name }}
            </p>
          </div>
          <!--  -->
          <div style="display: flex; flex-wrap: wrap">
            <p
              v-for="bookStatus in bookStatusList"
              style="margin-right: 10px; margin-bottom: 5px"
              :key="bookStatus.value"
              :class="{ activeCss: bookStatusValue == bookStatus.value }"
              @click="select(bookStatus.value, 'bookStatus')"
            >
              {{ bookStatus.name }}
            </p>
          </div>
          <!--  -->
          <div style="display: flex; flex-wrap: wrap">
            <p
              v-for="wordCount in wordCountList"
              style="margin-right: 10px; margin-bottom: 5px"
              :key="wordCount.value"
              :class="{ activeCss: wordCountValue == wordCount.value }"
              @click="select(wordCount.value, 'wordCount')"
            >
              {{ wordCount.name }}
            </p>
          </div>
        </div>
      </transition>
    </div>

    <van-list
      :immediate-check="false"
      v-model="loading"
      :finished="finished"
      finished-text=""
      @load="onLoad"
    >
      <!-- : -->
      <van-empty v-if="nullStatus" description="" />
      <!-- : -->
      <van-empty v-if="nullStatus2" description="" />
      <!--  -->
      <van-row
        gutter="20"
        style="
          padding: 0 5%;
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
        "
      >
        <van-col
          span="8"
          v-for="(book, index) in bookList"
          :key="index"
          style="margin-bottom: 10px; width: 30%"
          @click="enterBookDetail(book.id)"
        >
          <img
            width="100%"
            :src="book.cover + '?imageView&thumbnail=240y336'"
            @error="errorImg"
          />

          <p
            style="
              text-align: left;
              font-size: 14px;
              margin-top: 10px;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              overflow: hidden;
            "
          >
            {{ book.title }}
          </p>
          <p
            style="
              text-align: left;
              font-size: 14px;
              color: gray;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              overflow: hidden;
            "
          >
            {{ book.author }}
          </p>
        </van-col>

        <van-col span="8" style="margin-bottom: 10px; width: 30%"> </van-col>
      </van-row>
    </van-list>

    <div class="bottom_trim" style="margin-top: 50px"></div>
  </div>
</template>
<script>
import TitleBar from "../components/TitleBar.vue";
export default {
  components: { TitleBar },
  data() {
    return {
      shaixuan: false,
      zhanweiShow: false,
      shaixuanHeight: 0,
      top: "",
      // 
      loading: false,
      finished: false,
      // 
      // 0 1 2 3
      payList: [],
      payValue: 0,
      //0 1 2
      bookStatusList: [],
      bookStatusValue: 0,
      // 0 130 230-50 350-100 4100
      wordCountList: [],
      wordCountValue: 0,
      // 1 3
      sortList: [],
      sortValue: 1,
      categories: [],
      // 
      pageValue: 1,
      // 
      bookList: [],
      // 
      nullStatus: true,
      nullStatus2: false,
    };
  },
  // // // //
  beforeRouteEnter(to, from, next) {
    to, from;
    next((vm) => {
      // ,
      if (from.path == "/bookdetail") {
        console.log(",");
      } else {
        // 
        vm.$axios
          .get(`https://apis.netstart.cn/yunyuedu${vm.$route.query.url}`)
          .then((res) => {
            res.data.categories;
            vm.$nextTick(() => {
              vm.categories = res.data.categories;
              vm.categories.forEach((element) => {
                if (element.type == "pay") {
                  vm.payList = element.catArray;
                } else if (element.type == "bookStatus") {
                  vm.bookStatusList = element.catArray;
                } else if (element.type == "wordCount") {
                  vm.wordCountList = element.catArray;
                } else if (element.type == "sort") {
                  vm.sortList = element.catArray;
                }
              });
              vm.onLoad();
            });
          });
      }
    });
  },
  beforeRouteLeave(to, from, next) {
    to, from;
    if (to.path == "/bookmall/selected") {
      // ,
      console.log(to, from);
      this.shaixuan = false;
      this.zhanweiShow = false;
      this.shaixuanHeight = 0;
      this.top = "";
      // 
      this.loading = false;
      this.finished = false;
      // 
      // 0 1 2 3
      this.payList = [];
      this.payValue = 0;
      //0 1 2
      this.bookStatusList = [];
      this.bookStatusValue = 0;
      // 0 130 230-50 350-100 4100
      this.wordCountList = [];
      this.wordCountValue = 0;
      // 1 3
      this.sortList = [];
      this.sortValue = 1;
      this.categories = [];
      // 
      this.pageValue = 1;
      // 
      this.bookList = [];
      // 
      this.nullStatus = true;
      this.nullStatus2 = false;
    } else {
      //
    }

    next();
  },
  methods: {
    // 
    enterBookDetail(bookId) {
      this.$router.push({
        name: "bookdetail",
        query: {
          id: bookId,
        },
      });
    },
    // 
    select(selectValue, type) {
      console.log(":", selectValue);
      // 
      if (type == "sort") {
        this.sortValue = selectValue;
      } else if (type == "pay") {
        this.payValue = selectValue;
      } else if (type == "bookStatus") {
        this.bookStatusValue = selectValue;
      } else if (type == "wordCount") {
        this.wordCountValue = selectValue;
      }
      // 
      this.finished = false;
      this.nullStatus = true;
      this.bookList = [];
      this.pageValue = 2;
      this.axios
        .get(
          `https://apis.netstart.cn/yunyuedu/source/v2/searchBook.json?${this.getCaption(
            this.$route.query.url
          )}&wordCount=${this.wordCountValue}&pay=${this.payValue}&bookStatus=${
            this.bookStatusValue
          }&sort=${this.sortValue}`
        )
        .then((res) => {
          this.$nextTick(() => {
            if (res.data.list) {
              this.nullStatus = false;
              this.bookList = [];
              this.pageValue = 2;
              this.bookList = res.data.list;
            } else {
              this.nullStatus = false;
              this.nullStatus2 = true;
            }
          });
        });
      // ,300,
      if (this.top > 300) {
        this.$nextTick(() => {
          window.scrollTo(0, 0);
        });
      }
    },
    getCaption(str) {
      var index = str.lastIndexOf("?"); //?
      str = str.substring(index + 1, str.length);
      return str;
    },
    onLoad() {
      // 
      console.log(
        ":",
        this.$route.query.url,
        ":",
        this.wordCountValue,
        ":",
        this.payValue,
        ":",
        this.bookStatusValue,
        ":",
        this.sortValue,
        ":",
        this.pageValue
      );

      this.axios
        .get(
          `https://apis.netstart.cn/yunyuedu/source/v2/searchBook.json?${this.getCaption(
            this.$route.query.url
          )}&wordCount=${this.wordCountValue}&pay=${this.payValue}&bookStatus=${
            this.bookStatusValue
          }&sort=${this.sortValue}&page=${this.pageValue}`
        )
        .then((res) => {
          if (res.data.list) {
            this.nullStatus = false;
            this.pageValue++;
            this.bookList.push(...res.data.list);
            // 
            this.loading = false;
          }

          if (res.data.next == null || res.data.next == undefined) {
            // 
            this.loading = false;
            this.finished = true;
          }
        });
    },
    toDetail() {
      console.log(1);
    },

    toshow() {
      // 
      if (window.pageYOffset === 0) {
        // ,
        if (this.shaixuan == false && window.pageYOffset === 0) {
          // ---
          this.shaixuan = !this.shaixuan;
          this.$nextTick(() => {
            //,--,true,
            this.shaixuanHeight = this.$refs.tanchuang.offsetHeight + "px";
            this.zhanweiShow = true;
          });
          // ,
        } else if (this.shaixuan == true && window.pageYOffset === 0) {
          //,
          this.shaixuan = !this.shaixuan;
          // ,false
          this.zhanweiShow = false;
        }
      }
      // 
      if (window.pageYOffset > 0) {
        // ,
        if (this.shaixuan == false && window.pageYOffset > 0) {
          // ,,,
          this.shaixuan = !this.shaixuan;
          // ,,,,,true
          this.$nextTick(() => {
            this.shaixuanHeight = this.$refs.tanchuang.offsetHeight + "px";
            this.zhanweiShow = true;

            // 
            window.scrollBy(0, -this.$refs.tanchuang.offsetHeight);
          });
          // ,
        } else if (this.shaixuan == true && window.pageYOffset > 0) {
          // ,
          this.shaixuan = !this.shaixuan;
          //   ,,,,,false
          this.zhanweiShow = false;
          // 
          setTimeout(() => {
            window.scrollBy(0, this.$refs.tanchuang.offsetHeight);
          }, 300);
        }
      }
    },
    // 
    errorImg(event) {
      let img = event.srcElement;
      img.src = require("@/assets/img/book_cover.png");
      img.onerror = null; //
    },
  },

  // 
  mounted() {
    window.addEventListener("scroll", () => {
      this.top =
        document.documentElement.scrollTop ||
        document.body.scrollTop ||
        window.pageYOffset;
    });
    window.onresize = () => {
      this.$nextTick(() => {
        this.shaixuanHeight = this.$refs.tanchuang.offsetHeight + "px";
        // window.location.reload();
      });
    };
  },

  destroyed() {
    //window.onresize
    window.onresize = null;
  },
  // top
  watch: {
    // ,top,
    top(newValue, oldValue) {
      // 1,
      if (newValue > 1) {
        //   //,,
        if (newValue > oldValue && this.shaixuan == true) {
          // ,()
          this.shaixuan = !this.shaixuan;
          this.zhanweiShow = false;
        }
      }
    },
  },
};
</script>

<style>
.zhanwei {
  /*  */
}
.animate__animated,
.animate__bounce {
  --animate-duration: 0.4s;
}
.activeCss {
  color: red;
}
</style>