import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        genderNum: 1,
        thumbnail: "?imageView&thumbnail=240y336",

        // ID
        source_uuid: null,
        // 
        comment_result: Object,
        // 
        comment_num: null,
        // 
        back_home_key: '/bookmall/selected',
        // ID
        activatedIdVuex: null,


        // // ,
        localBookShelfx: null,

        // ,,--ID ---(ID!) --!
        historyProgress: Object,
        // 
        bookmall_nav_index: 0,
        // ID
        maleUrl: '7',
        // ID
        femaleUrl: '8',
        // ID
        publishUrl: '2000',
        // ID
        listenUrl: '30000',

    },
    //mutations--(state)
    mutations: {
        CHANGE_GENDERNUM(state, genderNum) {
            state.genderNum = genderNum;
        },

        GET_SOURCE_UUID(state, source_uuid) {
            state.source_uuid = source_uuid;
        },
        GET_COMMENT_RESULT(state, comment_result) {
            state.comment_result = comment_result;
            state.comment_num = comment_result.data.all.totalcount;

        },
        GET_BACK_HOME_KEY(state, back_home_key) {
            state.back_home_key = back_home_key;

        },
        GET_ACTIVATED_ID(state, activatedIdVuex) {
            state.activatedIdVuex = activatedIdVuex;
        },
        UPDATE_LOCAL_BOOKSHELF(state, localBookShelf) {
            state.localBookShelfx = localBookShelf;
        },
        GET_BOOKMALL_NAV_INDEX(state, bookmall_nav_index) {
            state.bookmall_nav_index = bookmall_nav_index;
        },
        GET_MALE_URL(state, maleUrl) {
            state.maleUrl = maleUrl;
        },
        GET_FEMALE_URL(state, femaleUrl) {
            state.femaleUrl = femaleUrl;
        },
        GET_PUBLISH_URL(state, publishUrl) {
            state.publishUrl = publishUrl;
        },
        GET_LISTEN_URL(state, listenUrl) {
            state.listenUrl = listenUrl;
        },
    },
    actions: {
        changeGenderNum(context) {
            if (context.state.genderNum == 1) {
                context.commit('CHANGE_GENDERNUM', 2);
            } else {
                context.commit('CHANGE_GENDERNUM', 1)
            }
        },

        // ID
        getSourceUuId(context, obj) {
            context.commit('GET_SOURCE_UUID', obj);
        },
        // 
        getCommentResult(context, obj) {
            context.commit('GET_COMMENT_RESULT', obj);
        },
        // 
        getBackHomeKey(context, obj) {
            context.commit('GET_BACK_HOME_KEY', obj);
        },
        // ID
        getActivatedId(context, obj) {
            context.commit('GET_ACTIVATED_ID', obj);
        },
        // ,
        updateLocalBookShelf(context, obj) {
            context.commit('UPDATE_LOCAL_BOOKSHELF', obj);
        },
        // 
        getBookmallNavIndex(context, obj) {
            context.commit('GET_BOOKMALL_NAV_INDEX', obj);
        },
        getMaleUrl(context, obj) {
            context.commit('GET_MALE_URL', obj);
        },

        getFemaleUrl(context, obj) {
            context.commit('GET_FEMALE_URL', obj);
        },
        getPublishUrl(context, obj) {
            context.commit('GET_PUBLISH_URL', obj);
        },
        getListenUrl(context, obj) {
            context.commit('GET_LISTEN_URL', obj);
        },

    },
    getter: {

    },
    modules: {}
})