<template>
  <!--  -->
  <van-pull-refresh
    success-text="!"
    v-model="isLoading"
    :head-height="80"
    @refresh="onRefresh"
  >
    <div>
      <!-- -->
      <van-swipe
        v-if="banner_cover"
        class="my-swipe"
        :autoplay="3000"
        indicator-color="#1baeae"
      >
        <van-swipe-item
          @click="
            $router.push({
              path: '/bookcontent',
              query: { id: item.id, homeKey: '/bookmall/listen' },
            })
          "
          v-for="(item, index) in banner_cover"
          :key="index"
        >
          <img class="swipe-img" :src="item.cover" alt="" />
        </van-swipe-item>
      </van-swipe>

      <!--  -->
      <div class="underline"></div>
      <!-- 51 : -->
      <FiveBook :five_book_sort="five_book_sort" />

      <!-- 31:  -->
      <ColumnThree :column_three_book_sort="column_three_book_sort" />
      <!--  -->

      <!-- 1 : -->
      <RowFour :row_four_book_sort="row_four_book_sort" />
    </div>
    <BaseLine v-if="banner_cover.length > 0" />

    <div class="bottom_trim"></div>
  </van-pull-refresh>
</template>

<script>
import { mapState } from "vuex";
import FiveBook from "../components/FiveBook.vue";
import ColumnThree from "../components/ColumnThree.vue";

import BaseLine from "./BaseLine.vue";
import RowFour from "./RowFour.vue";
export default {
  components: {
    FiveBook,
    ColumnThree,

    BaseLine,
    RowFour,
  },
  data() {
    return {
      ctype: 1,
      // highly_recommended_week: {},
      information: [],
      banner_cover: [],
      shortCut: [],
      isLoading: false,
      //  51 : 
      five_book_sort: null,

      //  31: 
      column_three_book_sort: null,
      //  

      //  1 :
      row_four_book_sort: null,
    };
  },

  methods: {
    // 
    onRefresh() {
      setTimeout(() => {
        this.axios
          .get(
            `https://apis.netstart.cn/yunyuedu/store/navi.json?gender=${this.genderNum}`
          )
          .then((res) => {
            // console.log(res);
            this.information = res.data.data.navi;
            console.log("selected-nav-information===>", this.information);
            // 
            this.axios
              .get(
                `https://apis.netstart.cn/yunyuedu/${this.information[5].url}`
              )
              .then((result) => {
                // 
                this.banner_cover = result.data.data.list[0].banners;

                //  51 : 
                this.five_book_sort = result.data.data.list[1];

                //  31: 
                this.column_three_book_sort = result.data.data.list[2];
                //  

                //  1 :
                this.row_four_book_sort = result.data.data.list[4];
              });
          });
        this.isLoading = false;
      }, 1000);
    },
  },
  beforeRouteLeave(to, from, next) {
    // ,

    this.$store.dispatch("getBackHomeKey", from.path);
    next();
  },
  beforeRouteEnter(to, from, next) {
    to, from;

    next((vm) => {
      vm.$store.dispatch("getBookmallNavIndex", 5);
      vm.$toast({
        message: ",!.",
        duration: 3000,
      });
    });
  },

  created() {
    //
    this.$toast.loading({
      message: "...",
      forbidClick: true,
      loadingType: "spinner",
      duration: 0,
      five_book_sort: null,
    });
    this.axios
      .get(
        `https://apis.netstart.cn/yunyuedu/store/navi.json?gender=${this.genderNum}`
      )
      .then((res) => {
        // console.log(res);

        this.information = res.data.data.navi;
        console.log("selected-nav-information===>", this.information);
        // 
        this.axios
          .get(`https://apis.netstart.cn/yunyuedu/${this.information[5].url}`)
          .then((result) => {
            // 
            this.banner_cover = result.data.data.list[0].banners;

            //  51 : 
            this.five_book_sort = result.data.data.list[1];

            //  31: 
            this.column_three_book_sort = result.data.data.list[2];
            //  

            //  1 :
            this.row_four_book_sort = result.data.data.list[4];

            this.$toast.clear();
          });
      });
  },
  computed: {
    ...mapState(["genderNum"]),
  },
  watch: {
    // watch,,
    genderNum: function (newvalue, oldvalue) {
      console.log(newvalue, oldvalue);

      // 
      this.$toast.loading({
        message: "...",
        forbidClick: true,
        loadingType: "circular",
      });
      this.axios
        .get(
          `https://apis.netstart.cn/yunyuedu/store/navi.json?gender=${newvalue}`
        )
        .then((res) => {
          // console.log(res);
          this.information = res.data.data.navi;
          console.log("selected-nav-information===>", this.information);
          // 
          this.axios
            .get(`https://apis.netstart.cn/yunyuedu/${this.information[5].url}`)
            .then((result) => {
              // 
              this.banner_cover = result.data.data.list[0].banners;

              //  51 : 
              this.five_book_sort = result.data.data.list[1];

              //  31: 
              this.column_three_book_sort = result.data.data.list[2];
              //  

              //  1 :
              this.row_four_book_sort = result.data.data.list[4];
            });
        });
    },
  },
};
</script>
<style lang="less" scoped>
// div
.bottom_trim {
  content: "";
  height: 50px;
  display: block;
}
.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  // width: 100%;
  // height: 150px;
  text-align: center;
  /* background-color: #39a9ed; */
}

.swipe-img {
  // width: 100%;
  // height: 150px;
}
.ranking_nav {
  ul {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 10px;
    margin-bottom: 10px;
    li {
      .yuan_color {
        width: 50px;
        height: 50px;
      }
    }
  }
}
// div,,
.underline::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background: #fffafa;
  transform: scaleY(1);
}
</style>
