<template>
  <!--  -->

  <div @scroll="loadMore($event)" class="BookCatalogView">
    <TitleBar :title="title" />
    <!-- -->
    <div v-if="bookInfo" class="top">
      <div class="left_img">
        <img
          :src="bookInfo.coverImage + thumbnail"
          alt=""
          @error="errorImg"
        />
      </div>
      <div class="right_txt">
        <div class="frame">
          <p>{{ bookInfo.title }}</p>
          <span>{{ bookInfo.author }}</span>
        </div>
      </div>
    </div>
    <!--  -->
    <div
      style="
        padding-left: 5%;
        text-align: left;
        margin-left: 10px;
        margin-bottom: 20px;
      "
    >
      {{ bookCatalog.length }}
    </div>
    <div class="catalog">
      <ul>
        <!--  -->
        <li
          v-for="(chapter, index) in bookCatalog.slice(startNum, endNum)"
          :key="chapter.secId"
          ref="chapter"
          @click="sendChapterIndex_secId(chapter.secId, index)"
        >
          <h2>
            <p>
              {{ chapter.title }}
              <span v-if="chapter.needPay" class="iconfont icon-suotou"></span>
            </p>
          </h2>
          <div class="dashline"></div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import TitleBar from "../components/TitleBar.vue";
export default {
  components: { TitleBar },
  data() {
    return {
      title: "",
      // 
      bookCatalog: [],
      // 
      bookInfo: null,
      startNum: 0,
      endNum: 100,
      newList: [],
      // 
      localHistory: {},
    };
  },
  watch: {
    // watch,,
    endNum: function (newvalue, oldvalue) {
      console.log(newvalue, oldvalue);
      // 
      this.$toast.loading({
        message: "...",
        forbidClick: true,
        loadingType: "circular",
        duration: 200,
      });
    },
  },
  computed: {
    ...mapState(["source_uuid"]),
    ...mapState(["thumbnail"]),
  },
  /* created() {
    // 
    this.$toast.loading({
      message: "...",
      forbidClick: true,
      loadingType: "circular",
      duration: 0,
    });
    // 
    this.axios
      .get(
        `https://apis.netstart.cn/yunyuedu/reader/book/info.json?source_uuid=${this.$route.query.id}`
      )
      .then((res) => {
        // bookCatalog
        this.$nextTick(() => {
          if (res.data.data == null) {
            this.$toast({
              message:
                "IDXML,,!",
              position: "bottom",
            });
            return;
          } else {
            this.bookCatalog = res.data.data.catalog;
            this.bookInfo = res.data.data.book;
            this.$toast.clear();
          }
        });
      });
  }, */
  // 
  activated() {},
  beforeRouteEnter(to, from, next) {
    console.log(to);
    console.log(from);
    if (from.path == "/bookcontent") {
      console.log("");
      next();
    } else {
      next((vm) => {
        if (vm.source_uuid != vm.$route.query.id) {
          console.log(
            "(),vm.source_uuid != vm.$route.query.id"
          );
          // 
          vm.$toast.loading({
            message: "...",
            forbidClick: true,
            loadingType: "circular",
            duration: 0,
          });
          // 
          vm.axios
            .get(
              `https://apis.netstart.cn/yunyuedu/reader/book/info.json?source_uuid=${vm.$route.query.id}`
            )
            .then((res) => {
              // cretaed,2,,(--------------.created)
              // bookCatalog
              vm.$nextTick(() => {
                if (res.data.data == null) {
                  vm.$toast({
                    message:
                      "IDXML,,!",
                    position: "bottom",
                    duration: 2500,
                  });
                  return;
                } else {
                  vm.bookCatalog = res.data.data.catalog;
                  vm.bookInfo = res.data.data.book;
                  vm.$toast.clear();
                }
              });
            });
        }
      });
    }
  },
  // 
  deactivated() {},
  mounted() {},
  methods: {
    // 
    errorImg(event) {
      let img = event.srcElement;
      img.src = require("@/assets/img/book_cover.png");
      img.onerror = null; //
    },
    // 
    sendChapterIndex_secId(secId, index) {
      index;
      // 
      // push,
      this.localHistory[this.$route.query.id] = {
        bookId: this.$route.query.id,
        secId: secId,
        time: new Date().getTime(),
      };
      localStorage.setItem("localHistory", JSON.stringify(this.localHistory));
      // 
      this.$router.push({
        path: "/bookcontent",
        query: {
          id: this.$route.query.id,
        },
      });
    },

    // 
    loadMore(e) {
      if (
        e.srcElement.scrollTop + e.srcElement.offsetHeight >=
        e.srcElement.scrollHeight
      ) {
        if (this.endNum < this.bookCatalog.length) {
          this.endNum = this.endNum + 250;
        } else {
          this.$toast({
            message: "!",
            position: "bottom",
          });
        }
      }
    },
  },
};
</script>

<style lang="less" scoped>
.BookCatalogView {
  height: 100vh;
  overflow: scroll;
  .top {
    display: flex;
    margin-left: 5%;
    margin-top: 7%;
    .left_img {
      img {
      }
    }
    .right_txt {
      // background: red;
      // padding-top: 40px;
      margin-left: 5%;
      width: 100%;
      text-align: left;
      .frame {
        margin-top: 20px;
        // background: red;
        p {
          font-size: 20px;
          margin-bottom: 10px;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 1;
          overflow: hidden;
        }
        span {
          color: gray;
          font-size: 16px;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 1;
          overflow: hidden;
        }
      }
    }
  }
  .catalog {
    // height: 100%;
    // overflow: auto;
    ul {
      text-align: left;
      padding: 0 10px;
      li {
        padding-left: 5%;
        h2 {
          margin-bottom: 5px;
          p {
            position: relative;
            span {
              position: absolute;
              right: 20px;
            }
          }
        }

        .dashline {
          border: 1px gray dashed;
          margin-bottom: 5px;
        }
      }
    }
  }
  .highLight {
    color: red;
  }
}
</style>